import GlobalSettings from '../components/Settings/GlobalSettings'
import { hillo, IKDataEntity } from 'innerken-utils'
import Servant from './Servant'

const entity = {
  id: {
    type: IKDataEntity.Types.Integer,
    displayName: 'id',
    tableConfig: {
      align: 'start',
    },
    formConfig: {
      requiredNew: false,
    },
  },
  name: {
    displayName: 'sectionName',
  },
  servantId: {
    displayName: 'servantName',
    type: IKDataEntity.Types.Option,
    formConfig: {
      type: {
        selectItems: Servant.getList,
      },
    },
  },
  tableCount: {
    form: false,
  },
}
const config = {
  add: function (item) {
    return hillo.postWithUploadFile('Section.php?op=add', item, {
      showLoading: true,
    })
  },
  edit: function (item) {
    return hillo.postWithUploadFile('Section.php?op=update', item, {
      showLoading: true,
    })
  },
  remove: function (id) {
    return hillo.post('Section.php?op=delete', { id: id })
  },
  load: async function (filter) {
    return (await hillo.get('Section.php', {
      op: 'view',
      lang: GlobalSettings.lang(),
      ...filter,
    }))
      .content.map(function (item) {
        return IKDataEntity.parseDataForEntity(item, entity)
      })
  },
}

export default IKDataEntity.ModelFactory(entity, config)
